.section {
    padding: 10px 0;

    .sec_item {
        position: relative;
        width: 100%;
        display: flex;
        justify-content: space-between;
        padding: 15px 0;

        .sec_left {

            h3 {
                font-weight: 600;
                padding-bottom: 10px;
            }

            p {
                padding-bottom: 5px;
                font-size: 15px;
            }

            ul {
                list-style-type: disc;
                margin: 0 15px;
                font-size: 15px;

                li {
                    padding: 5px 0;
                }
            }

            .sec_desc {
                border-left: 5px solid rgba(203, 159, 239, 1);
                padding: 5px 5px;
            }
        }

        .sec_right {

            p {
                text-align: end;
                padding-bottom: 5px;
                font-size: 15px;
            }
        }
    }
}

.cer_sec {
    width: 47%;
}

.skill_sec {
    width: 47%;
}

#ex_listBox {
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;

    #ex_list {
        list-style-type: disc;

        >li {
            padding: 5px 0;

            >span {
                font-weight: 600;
            }

            .ex_sublistBox {
                padding-left: 20px;

                .ex_list_sublist {
                    list-style-type: circle;

                    >li {
                        padding: 5px 0;
                        font-size: 15px;
                    }
                }
            }
        }
    }
}

.code {
    background-color: rgb(235, 232, 232);
    color: rgb(181, 102, 246);
    border-radius: 10px;
    padding: 2px 8px;
    font-size: 13px;
}

.proj_list {

    >li {

        &:first-child {
            color: rgb(129, 129, 129);
        }
    }
}