@font-face {
    font-family: 'Paperozi';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/2408-3@1.0/Paperlogy-7Bold.woff2') format('woff2');
    font-weight: 700;
    font-display: swap;
}

#title {
    font-family: 'Paperozi', dotum;
    font-size: 1.5rem;
    padding: 10px 0;
    border-bottom: 1px solid rgba(203, 159, 239, 1);

    .highlight {
        position: relative;

        &::after {
            position: absolute;
            content: "";
            display: inline-block;
            height: 10px;
            width: 110%;
            bottom: 5px;
            left: 0;
            background-color: rgba(203, 159, 239, 0.4);
        }
    }
}