@font-face {
    font-family: 'Paperozi';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/2408-3@1.0/Paperlogy-7Bold.woff2') format('woff2');
    font-weight: 700;
    font-display: swap;
}

#about_contact_sec {

    #about_name {
        font-size: 2rem;
        font-weight: 900;
        padding: 10px 0;
        font-family: 'Paperozi', dotum;
    }

    #about_intro {
        padding: 5px 10px;
        margin: 5px 0;
        font-size: 1.2rem;
        border-left: 5px solid rgba(203, 159, 239, 1);

        >span {
            font-weight: 700;
            color: rgb(181, 102, 246);
        }
    }

    #about_channel_list {
        padding: 20px 0;
        list-style-type: disc;
        margin: 0 15px;

        .about_item {
            padding: 5px 0;
            font-size: 1rem;

            >span {
                background-color: rgba(203, 159, 239, 0.4);
                font-weight: 600;
            }

            >a {
                color: rgba(203, 159, 239, 1);
                font-weight: 600;
                text-decoration: underline;
            }
        }
    }
}

#intro_imageBox {
    padding: 10px;

    >img {
        border-radius: 10px;
    }
}

#intro_textBox {
    padding: 5px 0;

    >p {
        padding: 5px 3px;
        font-size: 1rem;
    }

    #intro_text_subtitle {
        color: rgb(181, 102, 246);
        font-weight: 700;
        font-size: 1.1rem;
    }
}

.intro_link {
    &:hover {
        border-bottom: 1px solid rgba(203, 159, 239, 1);
    }

    >span {
        margin-left: 3px;
        color: rgba(203, 159, 239, 1);
    }
}